@import url(https://fonts.googleapis.com/css2?family=Texturina:wght@300&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1;
  background-color: #003325; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.App {
  font-family: 'Texturina', serif;
  width: 100%; }
  .App__title {
    font-size: 35px;
    text-align: center;
    margin-bottom: 40px;
    font-weight: bold;
    color: #00ccff; }
  .App__content {
    display: flex;
    justify-content: space-between; }

@media only screen and (max-width: 600px) {
  .App__content {
    display: inline-block;
    width: 100%; } }

.Column {
  width: 100%;
  border: 1px solid #e1e4e8;
  border-radius: 5px;
  background-color: #e6e6e6;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 20px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content; }
  .Column__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 24px; }
  .Column__item-count {
    line-height: 30px;
    width: 30px;
    background-color: #00ccff;
    border-radius: 100%;
    display: inline-block;
    text-align: center;
    margin-right: 7px;
    color: #fff; }
  .Column__text {
    font-weight: bold;
    font-weight: 18px;
    letter-spacing: 1px; }
  .Column__btn {
    background-color: #00ccff;
    color: #fff;
    padding: 5px 7px;
    border-radius: 5px;
    cursor: pointer; }

@media only screen and (max-width: 600px) {
  .Column {
    display: inline-block;
    width: 100%;
    overflow-x: hidden; } }

.AddNewModal {
  position: fixed;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  border: 1px solid #5680f9; }
  .AddNewModal__backdrop {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(33, 33, 33, 0.3); }
  .AddNewModal__content {
    position: relative;
    z-index: 9;
    width: 350px;
    background-color: #fff;
    padding: 10px 15px;
    border-radius: 5px;
    box-sizing: border-box;
    margin-top: -10%; }
  .AddNewModal__title {
    font-weight: bold;
    letter-spacing: 0.5px;
    line-height: 30px;
    box-sizing: border-box;
    border-bottom: 1px solid #ccc;
    color: #5680f9; }
  .AddNewModal__task-status {
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .AddNewModal__radio {
    display: flex;
    justify-content: center;
    align-items: center; }
  .AddNewModal__task {
    margin: 7px 0 25px;
    box-sizing: border-box; }
  .AddNewModal__input {
    line-height: 30px;
    width: 100%;
    border-radius: 3px;
    border: 1px solid #ccc;
    text-indent: 7px; }
  .AddNewModal__action {
    text-align: right; }
  .AddNewModal__btn {
    border: 1px solid transparent;
    width: 70px;
    box-sizing: border-box;
    line-height: 25px;
    margin: 0 7px;
    border-radius: 3px;
    font-size: 15px;
    cursor: pointer; }
    .AddNewModal__btn--confirm {
      background-color: #5680f9;
      color: #fff;
      border-color: #5680f9; }
    .AddNewModal__btn--cancel {
      background-color: #fff;
      border-color: #666A95;
      color: #666A95; }
  .AddNewModal__btn:last-child {
    margin-right: 0; }

.Task {
  width: 100%;
  background-color: #4ad212;
  box-shadow: 0 3px 7px 0 rgba(110, 142, 247, 0.13);
  border: 1px solid #E1E2E8;
  border-radius: 3px;
  padding: 15px 10px;
  box-sizing: border-box;
  margin-bottom: 15px; }
  .Task__editing {
    flex-grow: 2;
    word-wrap: break-word;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box; }
  .Task__editing-bgr {
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(247, 248, 254, 0.5);
    width: 100vw;
    height: 100vh; }
  .Task__editor {
    width: 100%;
    margin-right: 5px;
    text-indent: 2px;
    font-size: 17px;
    box-sizing: border-box;
    z-index: 9; }
  .Task__editing-action {
    display: flex;
    z-index: 99; }
    .Task__editing-action i {
      cursor: pointer;
      color: #666666;
      margin-left: 5px; }
      .Task__editing-action i:first-child {
        color: #00DF82; }
  .Task__time {
    font-size: 13px;
    color: #666666;
    margin-bottom: 10px; }
    .Task__time i {
      margin-right: 5px; }
  .Task__main {
    display: flex; }
  .Task__content {
    flex-grow: 2;
    padding-right: 10px;
    word-wrap: break-word;
    font-size: 17px; }
  .Task__action {
    display: flex; }
  .Task__btn {
    margin: 0 5px;
    cursor: pointer; }
    .Task__btn:first-child {
      font-size: 20px;
      color: #5680f9; }
    .Task__btn:nth-child(2) {
      font-size: 20px;
      color: #FF2F2F; }

